import numberBox from '@/components/common/RHX/numberBox/numberBox';
export default {
	components: {
		numberBox
	},
	props: [
		'disabled',
		'itemsData',
		'indexNum',
		'dataList',
		'deleteMoveShow'
	],
	data() {
		return {
			disableds: this.disabled,
			dataLists: this.dataList,
		};
	},
	watch: {
		dataList: {
			handler(a, b) {
				console.log('----------------dataList--------------')
				this.dataLists = a;
			},
			deep: true
		},
		disabled: {
			handler(a, b) {
				console.log('----------------dataList--------------')
				this.disableds = a;
			},
			deep: true
		},
	},
	created() {},
	methods: {
		bindChange(e) {
			console.log(e, '步进器数据');
			this.dataLists.count = Number(e);
			this.$emit('tap-NumberStep', [this.dataLists, this.indexNum]);
		}
	}
};
